
<template>
 <el-main>
         <!-- <p>{{$route.query.order_id}}</p> -->
        <div class="info_title">
          <h3>订单详情</h3>
        </div>
        <div id="vue_det">
          
          <p>订单信息</p>
          <el-row>
            <el-col :span="12">订单编号：<span v-html="order.order_sn"></span> </el-col>
            <el-col :span="12">用户名：<span v-html="order.user_name"></span></el-col>
          </el-row>
          <el-row>
            <el-col :span="12">下单时间：<span v-html="order.create_time"></span></el-col>
            <el-col :span="12">支付时间：<span v-html="order.pay_time"></span></el-col>
          </el-row>
          <el-row>
            <el-col :span="12">发货时间：<span v-html="order.sent_time"></span></el-col>
            <el-col :span="12">确认收货时间：<span v-html="order.finsh_time"></span></el-col>
          </el-row>
        

       <p>商品详情</p>
        <el-table
          :data="goodsinfo"
          border>
          <el-table-column prop="goods_id" label="ID"  width="200">
          </el-table-column>
          <el-table-column prop="goods_name" label="商品名称"  width="200">
          </el-table-column>
          <el-table-column prop="goods_price" label="商品单价"  width="200">
          </el-table-column>
          <el-table-column prop="goods_number" label="商品件数"  width="200">
          </el-table-column>
          <el-table-column prop="goods_count" label="商品总价"  width="200">
          </el-table-column>
        </el-table>
        <el-row>
          <el-col :span="8"><div class="grid-content "></div></el-col>
          <el-col :span="8"><div class="grid-content"></div></el-col>
          <el-col :span="8"><h4>实付总金额：<span v-html="order.total_count"></span></h4> </el-col>
          
        </el-row>
        <p>收货人信息</p>
        <el-row>
          <el-col :span="12">收货人：<span v-html="order.consignee"></span>   </el-col>
          <el-col :span="12"></el-col>
        </el-row>
        <el-row>
          <el-col :span="12">联系电话：<span v-html="order.mobile"></span> </el-col>
          <el-col :span="12"></el-col>
        </el-row>
        <el-row>
          <el-col :span="12">收货人地址：<span v-html="order.order_sn"></span>   </el-col>
          <el-col :span="12"></el-col>
        </el-row>
        <p>物流信息</p>
        <el-row>
          <el-col :span="12">物流公司：<span v-html="order.consignee"></span>   </el-col>
          <el-col :span="12"></el-col>
        </el-row>
        <el-row>
          <el-col :span="12">物流单号：<span v-html="order.consignee"></span>   </el-col>
          <el-col :span="12"></el-col>
        </el-row>
      </div>
     </el-main>
</template>

<style>

   .info_title{width: 100%; }
   .info_title h3{text-align: left; line-height: 60px;}
   #vue_det p{  line-height: 50px; border-top: 1px dotted #666 }
</style>

<script>
  export default {
    data() {
      return {
        order: {
         
        },
        goodsinfo:[{
          goods_id:'11',
          goods_name:'22',
          goods_price:'22',
          goods_number:'33',
          goods_count:'444'
          
        },
        {
          goods_id:'11',
          goods_name:'22',
          goods_price:'22',
          goods_number:'33',
          goods_count:'444'
          
        }],
        
        
      };

    },
    mounted:function(){
      this.editgoods();
      
    },
    
    methods: {
        //编辑商品
      editgoods(){
          var _this=this;
          this.$axios({
              method:'get',
              url:this.url()+'/index.php/orders/orderinfo',
              params:{
                  order_id:_this.$route.query.order_id
              }
          }).then(function(res){
                res.data.data.goodsinfo.forEach(result => {
                    result.goods_count= result.goods_price * result.goods_number;         
                });  
               _this.order=res.data.data.orderinfo[0];
               _this.goodsinfo=res.data.data.goodsinfo;


              console.log(res.data.data);
              //alert("111");
          });
       }, 
      
    }
  };
</script>






